<template>
  <div class="xqlist">
    <!-- 头部 -->
        <div class="msg_heard">
            <div @click="back">
                <van-icon name="arrow-left" color="#fff"  />
            </div>
            <div><p class="p">列表详情</p></div>
            <div></div>
        </div>
        <!-- 标题 -->
        <div class="content_title"><h2>{{arr.title}}</h2></div>
        <!-- 作者 -->
        <div class="content_author">
          <div class="content_left">
              <div>
                <van-image
                  round
                  width="40px"
                  height="40px"
                  :src='arr.avatar'
                />
              </div>
              <!--  https://img01.yzcdn.cn/vant/cat.jpeg-->
              <div class="content_time">
                <p>作者:{{arr.author}}</p>
                <p class="time">{{new Date(arr.time).toLocaleDateString() }}</p>
              </div>
          </div>
          <div>
            <van-button icon="plus" round  type="primary" class="btn_color" @click="isshow">{{show1?'关注':'已关注'}}</van-button>
          </div>
        </div>
        <!-- 内容 -->
        <div class="neirong">
          <p>{{arr.content}}</p>
          <div v-for="(item,i) in arr.imageSrc" :key="i">
            <img :src="item" alt="" srcset="" @error="imgError" />
          </div>
        </div>
        <!-- 评论列表 -->
        <div v-for="(item,i) in pinglunlist" :key="i">
          <div class="content_author1" >
            <div class="content_left">
              <div>
                <van-image round width="40px" height="40px" :src='item.info.avatar' />
              </div>
              <!--  https://img01.yzcdn.cn/vant/cat.jpeg-->
              <!-- 回复评论 -->
              <div class="pinglun">
                <van-cell >
                    <div class="content_time" @click="huifupinlun(item)">
                      <p>作者:{{item.info.nickname}}</p>
                      <h6>{{item.content}}</h6>
                      <div class="content_btn">
                        <p class="time">{{new Date(item.create_time).toLocaleDateString() }}</p>
                        <button class="huifu" @click.stop="chakanhuifu(item)"> {{item.reply_num}}回复</button>
                      </div>
                    </div>
                </van-cell>
                <van-popup v-model="showshow"  closeable position="bottom"  :style="{ height: '30%' }">
                  回复评论的评论
                  <van-field
                    v-model="message"
                    autosize
                    autofocus
                    type="textarea"
                    maxlength="1500"
                    placeholder="请输入评论"
                    show-word-limit
                    class="text"
                  />
                  <div class="baocun">
                    <van-button type="primary" block  @click="gethuifupinglun" >评论</van-button>
                  </div>
                </van-popup>
              </div>
              

            </div>
        
            <div >
              <van-icon name="good-job" size="20"  @click="qxhuifudianzan(item)" color="red"   v-if="item.is_like"/>
              <van-icon name="good-job" size="20"  @click="huifudianzan(item)" v-else/>
              <span>{{item.like_count}}</span>
            </div>
          </div>
          <div v-show="item._id===clickid">
            <!-- //回复列表 -->
            <div class="content_author2" v-for="(item1,i) in huifulist" :key="i">
              <div class="content_left">
                <div>
                  <van-image round width="40px" height="40px" :src='item1.info.avatar' />
                </div>
                <!--  https://img01.yzcdn.cn/vant/cat.jpeg-->
                <div class="content_time">
                  <p>作者:{{item1.info.nickname}}</p>
                  <h6>{{item1.content}}</h6>
                  <div class="content_btn">
                    <p class="time">{{new Date(item1.create_time).toLocaleDateString()}}</p>
                    <!-- <button class="huifu">回复</button> -->
                  </div>
                </div>
              </div>
              <!-- <div> 
                <span>{{item1.like_count}}</span>
                <van-icon name="good-job" />
              </div> -->
            </div>
          
          </div>
        </div>
        
        <!-- 底部 -->
        <div class="xq_footer">
          <div class="pinglun">
            <van-cell >
                  <van-button round type="info" class="btn" @click="showPopup">写评论</van-button>
            </van-cell>
            <van-popup v-model="show"  closeable position="bottom"  :style="{ height: '40%' }">
              评论
              <van-field
                v-model="message"
                autosize
                autofocus
                type="textarea"
                maxlength="1500"
                placeholder="请输入评论"
                show-word-limit
                class="text"
              />
              <div class="baocun">
                <van-button type="primary" block  @click="getFabupinglun" >发布</van-button>
              </div>
            </van-popup>
          </div>
          <!-- 信息 -->
          <van-icon name="comment-o" size="20"  />
          <!-- 收藏 -->
          <div>
            <van-icon name="star" size="20"  @click="qxshoucang" color="red" v-if="arr.is_fav"/>
            <van-icon name="star" size="20"  @click="shoucang" v-else/>
          </div>
          
          <!-- 点赞 -->
          <div>
            <van-icon name="good-job" size="20"  @click="qxdianzhan" color="red"   v-if="arr.is_like"/>
            <van-icon name="good-job" size="20"  @click="dianzhan" v-else/>
          </div>
          
          <!-- 分享 -->
          <van-icon name="share" size="20" />
        </div>
        <div>
         
        </div>
  </div>
</template>

<script>
import { Cell, CellGroup ,Icon,Button,Popup,Image as VanImage,Field,Toast,Collapse, CollapseItem  } from 'vant';
import axios from 'axios'
export default {
  name:'listcontent',
  data() {
    return {
        arr:[],
        show1:true,
        show:false,
        value: '',
        message:'',
        pinglunlist:[],
        clickid:'',
        huifulist:[],
        showshow:false,
        huifuid:''
    }
  },
  components:{
    [Cell.name]:Cell,
    [CellGroup.name]:CellGroup,
    [Icon.name]:Icon,
    [Button.name]:Button,
    [VanImage.name]:VanImage,
    [Popup.name]:Popup ,
    [Field.name]:Field,
    [Toast.name]:Toast,
    [Collapse.name]:Collapse,
    [CollapseItem.name]:CollapseItem,  
   },
  methods:{
    back(){
        this.$router.go(-1);
    },
    isshow(){
      this.show1=!this.show1
      
    },
    // 查看回复列表
    chakanhuifu(item){
       this.clickid=item._id
       if(item.reply_num===0){
        this.clickid=''
       }else{
          axios({
                method:'post',
                url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_reply_list',
                data:{
                      "skip": 0, //起始条数
                      "limit": 10, //一次返回的条数
                      "user_id": localStorage.getItem('uid'), //用户ID
                      "reply_comment_id": item._id, //回复的评论ID comment_type为1时有效  comment_type为0时可以不填
                      "article_id": this.$route.params.id,//文章ID
                    }    
                }).then(res=>{
                    this.huifulist=res.data.data
                    console.log("回复列表",this.huifulist);
          })
       }
    },
    // 发布弹出层
    showPopup() {
      this.show = true;
    },
    // 发布评论
    getFabupinglun(){
      axios({
        method:'post',
        url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_comment',
        data:{
          "user_id": localStorage.getItem('uid'), //用户ID
          "article_id": this.$route.params.id, //文章ID
          "content": this.message, //内容
          "reply_comment_id": "", //回复的评论ID comment_type为1时有效  comment_type为0时可以不填
          "comment_type": 0 //回复类型，0表示回复文章，1表示回复评论
        }
      }).then(res=>{
        this.show = false
        Toast(res.data.mag)
        this.message=''
        this.pinglunliebiao()
        console.log('评论',res);
      })
    },
    // 回复评论
    huifupinlun(item){
      // console.log(1,item._id);
      this.huifuid=item._id
      this.showshow=true
    },
    // 回复评论的回复
    gethuifupinglun(){
      
      axios({
        method:'post',
        url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_comment',
        data:{
          "user_id": localStorage.getItem('uid'), //用户ID
          "article_id": this.$route.params.id, //文章ID
          "content": this.message, //内容
          "reply_comment_id": this.huifuid, //回复的评论ID comment_type为1时有效  comment_type为0时可以不填
          "comment_type": 1 //回复类型，0表示回复文章，1表示回复评论
        }
      }).then(res=>{ 
        this.showshow = false
        // Toast(res.data.mag)
        this.message='';
        this.pinglunliebiao();
        console.log('回复的评论',res);
      })
    },
    // 主界面与评论列表
    pinglun(){
        axios({
              method:'post',
              url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_detail',
              data:{
                      "article_id":this.$route.params.id,
                      'user_id':localStorage.getItem('uid')
                  }    
              }).then(res=>{
                  this.arr=res.data.data
                  console.log("详情",this.arr);
        })
        
    },
    // 评论列表
    pinglunliebiao(){
      axios({
              method:'post',
              url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_comment_list',
              data:{
                      "article_id":this.$route.params.id,
                      'user_id':localStorage.getItem('uid'),
                      "skip":0,
                      "limit":10 
                  }    
              }).then(res=>{
                  this.pinglunlist=res.data.data
                  console.log('评论',res);
                  console.log('评论',this.pinglunlist);
        })
    },
    // 文章点赞
    dianzhan(){
      axios({
        method:'post',
        url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/like',
        data:{
             'user_id':localStorage.getItem('uid'),
             'article_id':this.$route.params.id
        }
      }).then(res=>{
        this.pinglun()
        console.log('点赞',res);
      })
    },
    // 取消文章点赞
    qxdianzhan(){
      axios({
        method:'post',
        url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/unlike',
        data:{
             'user_id':localStorage.getItem('uid'),
             'article_id':this.$route.params.id
        }
      }).then(res=>{
        this.pinglun()
        console.log('取消点赞',res);
      })
    },
    // 评论点赞
    huifudianzan(item){
      console.log(item._id);
      axios({
          method:'post',
          url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/comment_like',
          data:{
              'user_id':localStorage.getItem('uid'),
              'comment_id':item._id
          }
        }).then(res=>{
          this.pinglunliebiao()
          console.log('评论点赞',res);
        })
    },
    // 取消评论点赞
    qxhuifudianzan(item){
        axios({
          method:'post',
          url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/comment_unlike',
          data:{
              'user_id':localStorage.getItem('uid'),
              'comment_id':item._id
          }
        }).then(res=>{
          this.pinglunliebiao()
          console.log('取消评论点赞',res);
        })
    },
    // 收藏 
    shoucang(){
        axios({
          method:'post',
          url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_fav',
          data:{
              'user_id':localStorage.getItem('uid'),
              'article_id':this.$route.params.id
          }
        }).then(res=>{
          this.pinglun()
          console.log('收藏',res);
        })
    },
    // 取消收藏
    qxshoucang(){
        axios({
          method:'post',
          url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/remove_fav',
          data:{
              'user_id':localStorage.getItem('uid'),
              'article_id':this.$route.params.id
          }
        }).then(res=>{
          this.pinglun()
          console.log('取消收藏',res);
        })
    }
   },
  created(){
    // console.log(this.$route.params.id);
    this.pinglun(),
    this.pinglunliebiao()
  }
}
</script>

<style lang="less" scoped>
.xqlist{
  margin-bottom: 52px;
  margin-top: 48px;
}
.msg_heard{
    display: flex;
    height: 45px;
    background-color: rgb(16, 173, 81);
    // line-height: 45px;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
    .p{
        color: #fff;
        text-align: center;
    }
}
.content_title{
  margin: 20px 10px;
  h2{width: 360px; word-break: break-all;  //长单词强制换行
    }
}
.content_author{
  display: flex;
  justify-content: space-between;
  padding: 10px 10px;
  align-items: center;
  border-bottom: 1px solid rgb(212, 222, 222);
  .content_left{
    display: flex;
    font-size: 8px;
    align-items:stretch;
    .content_time{
      margin-left: 10px;
      .time{
      margin-top: 10px;
    }
    }
    
  }
}
.btn_color{
  height: 20px;
  color: #000;
  background-color: #fff;
  padding: 15px;
}
.neirong{
  // width: 360px;
  padding: 10px;
  p{
    width: 360px; 
    // border: 1px solid;
    word-break: break-all;  //长单词强制换行
  }

  img{
    margin-top: 10px;
    width: 100%;
    height: 100%;
  }
}
.content_author1{
  display: flex;
  justify-content: space-between;
  padding: 10px 10px;
  align-items: stretch;
  border-bottom: 1px solid rgb(212, 222, 222);
  .content_left{
    display: flex;
    font-size: 8px;
    align-items:stretch;
    .content_time{
      margin-left: 10px;
      h6{
        width: 230px;
        // border: 1px solid;
      }
      .content_btn{
         display: flex;
         align-items: center;
         .huifu{
          margin-left: 10px;
          width: 50px;
          height: 20px;
          font-size: 2px;
          background-color: #fff;
          color: #000;
          border: 1px solid #ccc;
          border-radius: 10px;
         }
      }
      .time{
        margin-top: 5px;
    }
    }
    
  }
}
.content_author2{
  width: 80vw;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding: 10px 10px;
  align-items: stretch;
  border-bottom: 1px solid rgb(212, 222, 222);
  .content_left{
    display: flex;
    font-size: 8px;
    align-items:stretch;
    // justify-content: space-around;
    .content_time{
      margin-left: 10px;
      h6{
        width: 220px;
        // border: 1px solid;
      }
      .content_btn{
         display: flex;
         align-items: center;
         .huifu{
          margin-left: 10px;
          width: 50px;
          height: 20px;
          font-size: 2px;
          background-color: #fff;
          color: #000;
          border: 1px solid #ccc;
          border-radius: 10px;
         }
      }
      .time{
        margin-top: 5px;
    }
    }
    
  }
}
.xq_footer{
  background: #fff;
  height: 50px;
  border-top: 1px solid rgb(193, 188, 188);
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-left: 10px;
  position: fixed;
  bottom:0;
  right: 0;
  width: 98%;
  .van-cell{
    padding: 0;
  }
  .btn{
    width: 100%;
    height: 30px;
    background-color: #fff;
    color: #000;
    border: 1px solid ;
  }
  .pinglun{
    height: 30px;
    flex: 3;
    // background-color: #fff;
    // color: #000;
    // border: 1px solid ;
    .text{
      margin-top: 10px;
      padding: 10px;
    }
    .baocun{
      width: 350px;
      margin: 25px auto;
    }
    .van-overlay{
      background-color: rgba(0,0,0,0.1);
    }
  }
  .van-icon{
    margin: 0 10px;
  }
}
</style>